/**
 * 复制内容到粘贴板
 * content : 需要复制的内容
 * message : 复制完后的提示，不传则默认提示"复制成功"
 */
function copyToClip(content, message) {
  var aux = document.createElement("input");
  aux.setAttribute("value", content);
  document.body.appendChild(aux);
  aux.select();
  document.execCommand("copy");
  document.body.removeChild(aux);


  if (message == null) {
    alert("没有找到复制内容");
  } else {
    showCopyTip(message)
  }
}


let timer = null
function showCopyTip(message) {
  // clearTimeout(timer)
  let pNode = document.createElement("p");
  pNode.className = 'copy-success-tip'
  let messageNode = document.createTextNode(message);
  pNode.appendChild(messageNode);
  resumeArticle = document.getElementById('resume-article')
  resumeArticle.appendChild(pNode);

  timer = setTimeout(() => {
    resumeArticle.removeChild(pNode);
    timer = null
  }, 1000)
}

email.onclick = (e) => {
  copyToClip(e.target.innerText, '邮箱复制成功！')
}

phone.onclick = (e) => {
  copyToClip(e.target.innerText, '手机号复制成功！')
}

wechat.onclick = (e) => {
  copyToClip(e.target.innerText, '微信号复制成功！')
}
